@import 'flexbox';
@import 'breakpoints';

.App{
    width: 100vw;
    height: 100vh;
    @include flexbox(column, '', '');
    font-size: 14px;
    .navbar, .appBody, .detailPage{
        padding: 2rem 5rem;
        @include md-lg-screen{
            padding: 2rem 4rem;
        }
        @include sm-md-screen{
            padding: 1rem 2rem;
        }
        @include sm-below-screen{
            padding: 1rem 1rem;
        }
    }
    .navbar{
        height: 10vh;
        @include flexbox('', center, space-between);
        // box-shadow: 0px 0px 10px #2020201c;

        h1{
            @include sm-md-screen{
                font-size: 24px;
            }
            @include sm-below-screen{
                font-size: 18px;
            }
        }
        .themeButton{
            @include flexbox('', center, '');
            cursor: pointer;
            // height: 10vh;
            padding: 1rem 1rem;
            border-radius: 0.3rem;
            span{
                margin-left: 8px;
            }
            &:hover{
                box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.219);
            }
        }
    }

    .appBody{
        @include flexbox(column, '', '');
        height: 90vh;
        padding-top: 0;
        padding-bottom: 0;
        position: relative;
        .menu{
            width: 100%;
            margin-top: 2rem;
            @include flexbox('', center, space-between);
            @include md-below-screen{
                @include flexbox(column, center, '')
            }

            .searchBox, .filter{
                padding: 1rem 2rem;
            }
            .searchBox{
                width: 500px;
                @include md-lg-screen{
                    width: 350px;
                }
                @include md-below-screen{
                    width: 100%;
                    margin-bottom: 1rem
                }
                @include flexbox('', center, '');
                input{
                    flex: 1;
                    margin-left: 8px;
                    color: inherit;
                }
            }

            .filter{
                width: 200px;
                @include md-below-screen{
                    width: 100%;
                    margin-bottom: 1rem
                }
                @include flexbox('', center, space-between);
                cursor: pointer;
                position: relative;

                .dropDown{
                    width: 100%;
                    position: absolute;
                    top: calc(100% + 0.5rem);
                    left: 0;
                    z-index: 10;
                    @include flexbox(column, flex-start, '');
                    .dropDownItem{
                        padding: 0.5rem 1rem;
                        cursor: pointer;
                        width: 100%;
                        &:first-child{
                            border-radius: 0.3rem 0.3rem 0 0;
                        }
                        &:last-child{
                            border-radius: 0 0 0.3rem 0.3rem;
                        }
                    }
                }
            }
        }
        .flags{
            display: grid;
            overflow-y: auto;
            flex: 1;
            overflow-x: hidden;
            &::-webkit-scrollbar{
                background-color: inherit;
                width: 10px;
                display: none;
            }
            &::-webkit-scrollbar-track{
                background-color: inherit;
            }
            &::-webkit-scrollbar-thumb{
                background-color: #516a80;
                opacity: 0;
            }
            &:hover{

            }
            @include lg-xl-screen{
                grid-template-columns: 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr;
                grid-auto-rows: 1fr;
                gap: 3rem 2rem;
            }
            @include md-lg-screen{
                grid-template-columns: 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr;
                grid-auto-rows: 1fr;
                gap: 2rem 1rem;
            }
            @include sm-md-screen{
                grid-template-columns: 1fr 1fr ;
                grid-template-rows: 1fr 1fr 1fr;
                grid-auto-rows: 1fr;
                gap: 3rem 2rem;
            }
            @include xl-above-screen{
                grid-template-columns: 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr;
                grid-auto-rows: 1fr;
                gap: 4rem 5rem;
            }
            @include sm-below-screen{
                @include flexbox(column, center, '')
            }
            grid-auto-flow: row;
            margin-top: 2rem;
            box-sizing: border-box;
            .flag{
                @include flexbox(column, flex-start, '');
                min-width: 200px;
                max-width: 300px;
                padding-bottom: 2rem;
                transition: transform;
                transition-duration: 500ms;
                transform: scale(100%);
                &:hover{
                    transform: scale(105%);
                    cursor: pointer;
                }
                @include sm-below-screen{
                    min-width: 90vw;
                    max-width: 90vw;
                    margin-bottom: 1rem;
                }
                img{
                    width: 100%;
                    height: 210px;
                    @include lg-xl-screen{
                        height: 175px;
                    }
                    @include md-lg-screen{
                        height: 150px;
                    }
                    border-radius: 0.3rem 0.3rem 0 0;
                    object-fit: fill;
                }
                .flagName{
                    font-weight: 700;
                    font-size: 1.1rem;
                    margin: 1rem 1rem;
                }
                .flagData{
                    margin: 0 1rem 0.5rem 1rem;
                    .flagDataInfo{
                        font-weight: lighter;
                    }
                }
            }
        }
        .detailPage{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            overflow-y: scroll;
            .detailPageButton{
                cursor: pointer;
                box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.219);
                // height: 10vh;
                padding: 0.5rem 1rem;
                border-radius: 0.3rem;
            }
            .detailPageMenu{
                @include flexbox('', '', flex-start);
                div{
                    @include flexbox('', center, '');
                    margin-bottom: 4rem;
                    @include md-lg-screen{
                        margin-bottom: 4rem;
                    }
                    @include sm-md-screen{
                        margin-bottom: 2rem;
                    }
                    @include sm-below-screen{
                        margin-bottom: 2rem;
                    }
                    span{
                        margin-left: 8px;
                    }
                }
            }
            .detailPageContent{
                width: 100%;
                img{
                    width: 100%;
                    max-width: 550px;
                    display: block;
                    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.219);
                }   
                @include lg-above-screen{
                    @include flexbox('', center, space-between);
                    img{
                        max-width: 50%
                    }
                    .countryDetails{
                        width: 50%;
                    }
                }
                @include lg-below-screen{
                    @include flexbox(column, center, '');
                    img{
                        width: 100%
                    }
                    .countryDetails{
                        width: 100%;
                    }
                }
                .countryDetails{
                    @include flexbox(column, flex-start, space-between);
                    max-width: 550px;
                    @include lg-above-screen{
                        max-width: 45%;
                    }
                    @include md-above-screen{
                        height: 350px;
                    }
                    .countryDetails-main{
                        @include md-above-screen{ 
                            @include flexbox('', center, space-between);
                        }
                        @include md-below-screen{ 
                            @include flexbox(column, center, '');
                        }
                    }
                    padding: 1rem;
                    .countryDetails-main, .bordersDiv{
                        line-height: 200%;
                        width: 100%;
                        @include xl-above-screen{
                            width: 80%;
                        }
                        @include flexbox('', flex-start, space-between);
                        .countryDetails-a, .countryDetails-b{
                            @include flexbox(column, flex-start, '');
                            @include md-below-screen{
                                margin-bottom: 1rem;
                            }
                            .detailKey{
                                margin-right: 4px;
                            }
                            .detailValue{
                                font-weight: lighter;
                            }
                        }
                    }
                    .bordersDiv{
                        @include flexbox('', center, flex-start);
                        flex-wrap: wrap;
                        @include lg-below-screen{
                            flex-direction: column;
                            align-items: flex-start;
                        }
                        .detailKey{
                            margin-right: 8px;
                        }
                        margin-top: 1rem;
                        .borderButtonsDiv{
                            font-size: 10px;
                            @include flexbox('', '', '');
                            flex-wrap: wrap;
                            .borderButton{
                                padding: 0.5rem;
                                min-width: 80px;
                                max-height: 30px;
                                @include flexbox('', center, center);
                                margin: 4px;
                            }
                        }
                    }
                }
            }
        }
    }
    .darkElements{
        background-color: hsl(209, 23%, 22%);
        color: hsl(0, 0%, 100%);
        box-shadow: 0px 5px 10px 1px #2020204d;
        border-radius: 0.3rem;

        .dropDownItem{
            &:hover{
                background-color: #516a80;
            }
        }
    }
    .lightElements{
        background-color: hsl(0, 0%, 100%);
        box-shadow: 0px 5px 10px 1px #2020201c;
        border-radius: 0.3rem;

        .dropDownItem{
            &:hover{
                background-color: #cccccc;
            }
        }
    }
}


.darkApp{
    color: hsl(0, 0%, 100%);
    background-color: hsl(207, 26%, 17%);
}
.lightApp{
    color:  hsl(200, 15%, 8%);
    background-color: hsl(0, 0%, 98%);
}
